@import "main";
// End Of Import

.loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    @extend .Flex;
    background-color: #fff;
    z-index: 999999;
  
    img {
      animation: pulse 2s linear infinite;
    }
}
  
.navover {
position: fixed;
width: 100%;
height: 100%;
background-color: rgba($sec-color, 0.95);
z-index: 9;
transition: 0.5s;
opacity: 0;
visibility: hidden;

&.open {
    opacity: 1;
    visibility: visible;
}
}
  
.sideNav {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: $sec-color;
    z-index: 99;
    overflow-y: auto;
    transition: 0.5s;
    @extend .Flex;
    flex-direction: column;
    transform-origin: right;
    transform: scaleX(0);
  
    html[dir="ltr"] & {
      transform-origin: left;
    }
  
    &.open {
      // transform-origin: left;
      transform: scaleX(1);
  
      ul {
        opacity: 1;
  
        li {
          transform: translateX(0);
          opacity: 1;
        }
      }
    }
  
    &.origin {
      transform-origin: left;
  
      html[dir="ltr"] & {
        transform-origin: right;
      }
    }
  
    .options {
      padding: 0 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      position: absolute;
      top: 50px;
  
      @include mob480 {
        padding: 0 40px;
        top: 30px;
      }
  
      .lang a {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 14px;
        color: #fff;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        background-color: $main-color;
      }
  
      .close1 {
        svg {
          filter: brightness(0) invert(1);
        }
      }
    }
  
    & > ul {
      display: flex;
      align-items: center;
      flex-direction: column;
      opacity: 0;
      transition: 0.5s;
      transition-delay: 0.5s;
    //   overflow-y: auto;
  
      &::-webkit-scrollbar {
        width: 8px;
      }
  
      &::-webkit-scrollbar-track {
        background-color: $sec-color;
      }
  
      &::-webkit-scrollbar-thumb {
        // border-radius: 10px;
        background-color: $main-color;
      }
  
      @include mob480 {
        margin-top: 50px;
      }
  
      & > li {
        position: relative;
        padding: 7px 0;
        // margin-bottom: 20px;
        transition: 0.5s;
        transform: translateX(-10px);
  
        @include LTR {
          transform: translateX(10px);
        }
  
        opacity: 0;
  
        &:nth-child(1) {
          transition-delay: 0.2s;
        }
  
        &:nth-child(2) {
          transition-delay: 0.4s;
        }
  
        &:nth-child(3) {
          transition-delay: 0.6s;
        }
  
        &:nth-child(4) {
          transition-delay: 0.8s;
        }
  
        &:nth-child(5) {
          transition-delay: 1s;
        }
  
        &:nth-child(6) {
          transition-delay: 1.2s;
        }
  
        &:nth-child(7) {
          transition-delay: 1.4s;

            > a{
                position: relative;
                display: flex;
                align-items: center;

                &:after{
                    content: "";
                    width: 0;
                    height: 0;
                    border-left: 4px solid transparent;
                    border-right: 4px solid transparent;
                    border-top: 5px solid white;
                    transform: translateY(5px);
                    margin-right: 5px;
                }
            }
        }
  
        &:nth-child(8) {
          transition-delay: 1.6s;
        }
  
        &:nth-child(9) {
          transition-delay: 1.8s;
        }
  
        &:nth-child(10) {
          transition-delay: 2s;
        }
  
        &:nth-child(11) {
          transition-delay: 2.2s;
        }
  
        a {
          width: 100%;
          display: block;
          font-size: 18px;
          color: #fff;
          transition: 0.5s;
        }
  
        i {
          position: absolute;
          top: 13px;
          left: 13px;
          transform: rotate(90deg);
          font-size: 9px;
          color: currentColor;
          transition: 0.5s;
  
          html[dir="ltr"] & {
            left: unset;
            right: 13px;
          }
  
          &.open {
            transform: rotate(0deg);
          }
        }
  
        .drop {
          width: 100%;
          padding: 5px 15px;
          display: none;
          overflow: visible;
          height: auto;
  
          li {
            padding: 4px 0;
  
            a {
              font-size: 15px;
              color: #898989;
              transition: 0.5s;
  
              &:hover {
                color: #595959;
              }
            }
          }
        }
      }
    }
}

