// Header Slider
.header_slider{

    .Bullets_top{
        top: 0;
        bottom: unset;
        width: calc(43px * 6);
        display: flex;
        height: 100%;

        @include Sm{
            display: none;
        }

        span{
            display: block;
            width: 43px;
            padding: 10px;
            height: 100%;
            margin: 0 !important;
            border-radius: 0;
            opacity: 1;
            color: #fff;
            transition: .5s;

            &:not(:first-child){
                box-shadow: 7px 0 6px rgba(#000, .16);
            }

            &:nth-child(1){
                background-color: transparent;

                &.swiper-pagination-bullet-active{
                    background-color: transparent;
                }
            }
            &:nth-child(2){
                background-color: #C93C93;

                &.swiper-pagination-bullet-active{
                    background-color: lighten(#C93C93, 10%);
                }
            }
            &:nth-child(3){
                background-color: #FF005F;

                &.swiper-pagination-bullet-active{
                    background-color: lighten(#FF005F, 10%);
                }
            }
            &:nth-child(4){
                background-color: #FF8C00;

                &.swiper-pagination-bullet-active{
                    background-color: lighten(#FF8C00, 10%);
                }
            }
            &:nth-child(5){
                background-color: #FFC600;

                &.swiper-pagination-bullet-active{
                    background-color: lighten(#FFC600, 10%);
                }
            }
            &:nth-child(6){
                background-color: #952256;

                &.swiper-pagination-bullet-active{
                    background-color: lighten(#952256, 10%);
                }
            }
        }
    }

    .swiper-slide{
        // height: 377px;

        @include Sm{
            height: auto;
        }
        img{
            width: calc(100% - (43px * 5));

            @include Sm{
                width: auto;
            }
        }
    }

    .Bullets_bottom{
        position: absolute;
        bottom: 20px;
        right: 18px;
        z-index: 1;
        padding: 10px;
        background-color: rgba(#000, .6);
        border-radius: 15px;
        text-align: center;

        @include Sm{
            position: unset;
            border-radius: 0;
            margin-top: -1px;
        }

        .swiper-pagination{
            position: unset;
            span{
                width: 30px;
                height: 30px;
                border: 3px solid transparent;
                @extend .Flex;
                display: inline-flex;
                margin: 0 2.5px;
                border-radius: 0;
                color: #fff;
                opacity: 1 !important;
                transition: .5s;

                &.swiper-pagination-bullet-active{
                    border-color: #fff;
                }

                &:nth-child(1){
                    background-color: #F05921;
    
                    &.swiper-pagination-bullet-active{
                        background-color: lighten(#F05921, 10%);
                    }
                }
                &:nth-child(2){
                    background-color: #C93C93;
    
                    &.swiper-pagination-bullet-active{
                        background-color: lighten(#C93C93, 10%);
                    }
                }
                &:nth-child(3){
                    background-color: #FF005F;
    
                    &.swiper-pagination-bullet-active{
                        background-color: lighten(#FF005F, 10%);
                    }
                }
                &:nth-child(4){
                    background-color: #FF8C00;
    
                    &.swiper-pagination-bullet-active{
                        background-color: lighten(#FF8C00, 10%);
                    }
                }
                &:nth-child(5){
                    background-color: #FFC600;
    
                    &.swiper-pagination-bullet-active{
                        background-color: lighten(#FFC600, 10%);
                    }
                }
                &:nth-child(6){
                    background-color: #952256;
    
                    &.swiper-pagination-bullet-active{
                        background-color: lighten(#952256, 10%);
                    }
                }
            }
        }

        > span{
            font-size: 15px;
            color: #fff;
        }
    }
}
