// opinions Page
.opinions_page {
    padding-bottom: 70px;

    .content {
        max-width: 1000px;
        border-radius: 50px;
        box-shadow: 0 37px 40px rgba(#385298, .18);
        margin: auto;
        padding: 30px 70px;

        @include Sm{
            padding: 20px;
        }
    }

    h2{
        font-size: 18px;
        font-weight: 600;
        color: var(--sec-color);
    }

    .rating {
        margin-bottom: 60px;

        h2{
            margin-bottom: 30px;
        }

        .radios{
            width: 60%;
            margin: auto;
            display: flex;
            align-items: center;
            justify-content: space-between;

            @include Sm{
                width: 100%;
            }

            label{
                margin-bottom: 0;
            }

            input{
                opacity: 0;
                visibility: hidden;
                position: absolute;

                &:checked{

                    & + .svg_container{

                        &:after, &:before{
                            opacity: 1;
                        }
                    }
                }
            }

            .svg_container{
                position: relative;
                cursor: pointer;

                &:after{
                    content: "";
                    width: 60px;
                    height: 60px;
                    border-radius: 50%;
                    background-color: #EAEDF5;
                    display: block;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    z-index: -2;
                    opacity: 0;
                    @extend .trans;
                }
                
                &:before{
                    content: "";
                    width: 30px;
                    height: 30px;
                    border-radius: 50%;
                    background-color: #FFBA44;
                    display: block;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    z-index: -1;
                    opacity: 0;
                    @extend .trans;
                }
            }
        }
    }

    .opinion, .more_opinion{

        textarea{
            width: 100%;
            height: 140px;
            border: 1px solid var(--sec-color);
            border-radius: 5px;
            display: block;
            margin-bottom: 70px;
            padding: 15px;
            margin-top: 15px;
            @extend .trans;
            
            &:focus{
                outline: none;
                border-color: var(--main-color);
            }
        }

        h2{
            display: flex;
            align-items: center;
            svg{
                transform: scale(.7);
                margin-right: 10px;
            }
        }

        div{

            svg{
                transform: scale(.7);
                margin-left: 10px;
            }
        }
    }

    button{
        width: 140px;
        height: 40px;
        margin: auto;
        margin-top: 30px;
        background-color: var(--main-color);
        border-radius: 25px;
        @extend .Flex;
        color: #fff;
        font-weight: 600;
        font-size: 16px;
        @extend .trans;
        border: 1px solid transparent;

        &:hover{
            background-color: transparent;
            border-color: var(--main-color);
            color: var(--main-color);
        }
    }
}
