@mixin mob320 {
    @media (max-width: 320.5px) {
        @content;
    }
}
@mixin mob375 {
    @media (max-width: 375.5px) {
        @content;
    }
}
@mixin mob425 {
    @media (max-width: 425.5px) {
        @content;
    }
}
@mixin mob445 {
    @media (max-width: 445.5px) {
        @content;
    }
}
@mixin mob480 {
    @media (max-width: 480.5px) {
        @content;
    }
}
@mixin mob600 {
    @media (max-width: 600.5px) {
        @content;
    }
}

@mixin Xs {
    @media (max-width: 767.5px) {
        @content;
    }
}
//For Small
@mixin Sm {
    @media (max-width: 991.5px) {
        @content;
    }
}
//For Medium
@mixin Md {
    @media (min-width: 992.5px) {
        @content;
    }
}
//For Extra Large
@mixin Lg {
    @media (min-width: 1200.5px) {
        @content;
    }
}

//For medium Large
@mixin kh {
    @media (min-width: 1200.5px) and (max-width: 1600.5px) {
        @content;
    }
}

// English
@mixin LTR {
    html[dir="ltr"] & {
        @content;
    }
}
