.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 999999;
}

.loader img {
  animation: pulse 2s linear infinite;
}

.navover {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(17, 99, 110, 0.95);
  z-index: 9;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;
}

.navover.open {
  opacity: 1;
  visibility: visible;
}

.sideNav {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: #11636E;
  z-index: 99;
  overflow-y: auto;
  transition: 0.5s;
  flex-direction: column;
  transform-origin: right;
  transform: scaleX(0);
}

html[dir="ltr"] .sideNav {
  transform-origin: left;
}

.sideNav.open {
  transform: scaleX(1);
}

.sideNav.open ul {
  opacity: 1;
}

.sideNav.open ul li {
  transform: translateX(0);
  opacity: 1;
}

.sideNav.origin {
  transform-origin: left;
}

html[dir="ltr"] .sideNav.origin {
  transform-origin: right;
}

.sideNav .options {
  padding: 0 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  position: absolute;
  top: 50px;
}

@media (max-width: 480.5px) {
  .sideNav .options {
    padding: 0 40px;
    top: 30px;
  }
}

.sideNav .options .lang a {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #fff;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background-color: #F05921;
}

.sideNav .options .close1 svg {
  filter: brightness(0) invert(1);
}

.sideNav > ul {
  display: flex;
  align-items: center;
  flex-direction: column;
  opacity: 0;
  transition: 0.5s;
  transition-delay: 0.5s;
}

.sideNav > ul::-webkit-scrollbar {
  width: 8px;
}

.sideNav > ul::-webkit-scrollbar-track {
  background-color: #11636E;
}

.sideNav > ul::-webkit-scrollbar-thumb {
  background-color: #F05921;
}

@media (max-width: 480.5px) {
  .sideNav > ul {
    margin-top: 50px;
  }
}

.sideNav > ul > li {
  position: relative;
  padding: 7px 0;
  transition: 0.5s;
  transform: translateX(-10px);
  opacity: 0;
}

html[dir="ltr"] .sideNav > ul > li {
  transform: translateX(10px);
}

.sideNav > ul > li:nth-child(1) {
  transition-delay: 0.2s;
}

.sideNav > ul > li:nth-child(2) {
  transition-delay: 0.4s;
}

.sideNav > ul > li:nth-child(3) {
  transition-delay: 0.6s;
}

.sideNav > ul > li:nth-child(4) {
  transition-delay: 0.8s;
}

.sideNav > ul > li:nth-child(5) {
  transition-delay: 1s;
}

.sideNav > ul > li:nth-child(6) {
  transition-delay: 1.2s;
}

.sideNav > ul > li:nth-child(7) {
  transition-delay: 1.4s;
}

.sideNav > ul > li:nth-child(7) > a {
  position: relative;
  display: flex;
  align-items: center;
}

.sideNav > ul > li:nth-child(7) > a:after {
  content: "";
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 5px solid white;
  transform: translateY(5px);
  margin-right: 5px;
}

.sideNav > ul > li:nth-child(8) {
  transition-delay: 1.6s;
}

.sideNav > ul > li:nth-child(9) {
  transition-delay: 1.8s;
}

.sideNav > ul > li:nth-child(10) {
  transition-delay: 2s;
}

.sideNav > ul > li:nth-child(11) {
  transition-delay: 2.2s;
}

.sideNav > ul > li a {
  width: 100%;
  display: block;
  font-size: 18px;
  color: #fff;
  transition: 0.5s;
}

.sideNav > ul > li i {
  position: absolute;
  top: 13px;
  left: 13px;
  transform: rotate(90deg);
  font-size: 9px;
  color: currentColor;
  transition: 0.5s;
}

html[dir="ltr"] .sideNav > ul > li i {
  left: unset;
  right: 13px;
}

.sideNav > ul > li i.open {
  transform: rotate(0deg);
}

.sideNav > ul > li .drop {
  width: 100%;
  padding: 5px 15px;
  display: none;
  overflow: visible;
  height: auto;
}

.sideNav > ul > li .drop li {
  padding: 4px 0;
}

.sideNav > ul > li .drop li a {
  font-size: 15px;
  color: #898989;
  transition: 0.5s;
}

.sideNav > ul > li .drop li a:hover {
  color: #595959;
}

