﻿html {
    &::-webkit-scrollbar {
        width: 8px;
    }
    &::-webkit-scrollbar-track {
        background-color: $sec-color;
    }
    &::-webkit-scrollbar-thumb {
        // border-radius: 10px;
        background-color: $main-color;
    }

    // overflow-y: hidden;
    overflow-x: hidden;
    // Colors Vars
    --main-color: #F05921;
    --sec-color: #11636E;
    --text-color: #212121;
    scrollbar-width: thin;
    scrollbar-color: rgba($main-color, 1) rgba($sec-color, 1);
}

body {
    font-family: 'Tajawal', sans-serif;
    text-align: right;
}

* {
    font-family: inherit;
}

input,
a,
button {
    font-family: 'Tajawal', sans-serif;

    &:focus {
        outline: none;
        text-decoration: none;
    }

    &:hover {
        text-decoration: none;
    }
}

button {
    cursor: pointer;

    &:focus,
    &:active {
        outline: none;
        box-shadow: none;
    }
}

ul.res {
    padding: 0;
    margin: 0;
    list-style: none;
}
