.Flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

.trans{
    transition: 0.4s cubic-bezier(0.5, 1, 0.89, 1);
}

.page{
    padding-bottom: 50px;
}

.hover{
  
  &:hover{

    animation: flash .5s linear;
  }
}

@keyframes pulse {
    0% {
      opacity: 1;
    }
  
    50% {
      opacity: 0.6;
    }
  
    100% {
      opacity: 1;
    }
}

@keyframes flash {  
    0% {
      opacity: 0.7;
    }
  
    100% {
      opacity: 1;
    }
}